@import url(https://fonts.googleapis.com/css2?family=Pacifico&family=Roboto+Condensed:wght@400;700&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  font-family: "Pacifico", cursive;
  font-family: "Roboto Condensed", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.Background {
  height: 100vh;
  overflow: hidden;
}
.Circle1 {
  position: absolute;
  top: 10%;
  right: 15%;
  width: 240px;
  height: 240px;
  background: linear-gradient(135deg, #00fac1 0%, #c500e1 100%);
  border-radius: 100%;
}
.Circle2 {
  position: absolute;
  bottom: 10%;
  left: 15%;
  width: 240px;
  height: 240px;
  background: linear-gradient(135deg, #00fac1 0%, #c500e1 100%);
  border-radius: 100%;
}
.SharedComponent {
  width: 60%;
  border-radius: 25pt;
  -webkit-backdrop-filter: blur(20px);
  backdrop-filter: blur(20px);
  box-shadow: 0 4px 25px 5px rgba(106, 45, 128, 0.1);
  border: solid 1px rgba(255, 255, 255, 0.2);
  background: linear-gradient(
    116.34deg,
    rgba(255, 255, 255, 0.5) 0%,
    rgba(255, 255, 255, 0.05) 100%
  );
  padding: 50px;
  overflow-x: hidden;
}
.Main {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  height: 100vh;
}

.HomePageColor {
  background: linear-gradient(107.56deg, #5f72bd 0%, #9b23ea 100%);
}
.ProjectsPageColor {
  background: linear-gradient(72.44deg, #2c5593 0%, #5f72bd 100%);
  background-size: 400% 400%;
  animation: gradient 3s ease;
  animation-fill-mode: forwards;
}
.ContactPageColor {
  background: linear-gradient(107.56deg, #5f72bd 0%, #9b23ea 100%);
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

@media (min-width: 280px) and (max-width: 899px) {
  .Circle1 {
    width: 150px;
    height: 150px;
  }
  .Circle2 {
    width: 150px;
    height: 150px;
  }
  .Main {
    align-items: flex-start;
    margin-top: 20px;
  }
  .SharedComponent {
    width: 90%;
    height: 80%;
    padding: 20px;
  }
}

@media (min-width: 900px) and (max-width: 1350px) {
  .SharedComponent {
    width: 90%;
  }
}

@media (min-width: 1351px) and (max-width: 1610px) {
  .SharedComponent {
    width: 80%;
  }
}

.Circle1__Right__To__Left {
  animation: AnimateCircle1__Right__Left 1s;
  animation-fill-mode: forwards;
}
.Circle1__Left__To__Right {
  animation: AnimateCircle1__Left__Right 1s;
  animation-fill-mode: forwards;
}
@keyframes AnimateCircle1__Right__Left {
  from {
    top: 10%;
    right: 15%;
  }
  to {
    top: 5%;
    right: 70%;
  }
}
@keyframes AnimateCircle1__Left__Right {
  from {
    top: 5;
    right: 70%;
  }
  to {
    top: 10%;
    right: 15%;
  }
}
.Circle2__Left__To__Right {
  animation: AnimateCircle2__Left__Right 1s;
  animation-fill-mode: forwards;
}
.Circle2__Right__To__Left {
  animation: AnimateCircle2__Right__Left 1s;
  animation-fill-mode: forwards;
}
@keyframes AnimateCircle2__Left__Right {
  from {
    bottom: 10%;
    left: 15%;
  }
  to {
    bottom: 5%;
    left: 70%;
  }
}
@keyframes AnimateCircle2__Right__Left {
  from {
    bottom: 5%;
    left: 70%;
  }
  to {
    bottom: 10%;
    left: 15%;
  }
}

/* ///////////////////////////////////////////////// */
/* Card Animations */
.AnimateCurrentCard__Right__To__Left {
  position: absolute;
  animation: AnimateCurrentCard__Right__To__Left 0.5s;
  animation-fill-mode: forwards;
}

@keyframes AnimateCurrentCard__Right__To__Left {
  from {
    right: -100%;
  }
  to {
    right: 20%;
  }
}

.AnimatePrevCard__Right__To__Left {
  position: absolute;
  animation: AnimatePrevCard__Right__To__Left 0.5s;
  animation-fill-mode: forwards;
}

@keyframes AnimatePrevCard__Right__To__Left {
  from {
    left: 20%;
  }
  to {
    left: -100%;
  }
}
/* ////////////////// */
.AnimateCurrentCard__Left__To__Right {
  position: absolute;
  animation: AnimateCurrentCard__Left__To__Right 0.5s;
  animation-fill-mode: forwards;
}

@keyframes AnimateCurrentCard__Left__To__Right {
  from {
    left: -100%;
  }
  to {
    left: 20%;
  }
}

.AnimatePrevCard__Left__To__Right {
  position: absolute;
  animation: AnimatePrevCard__Left__To__Right 0.5s;
  animation-fill-mode: forwards;
}

@keyframes AnimatePrevCard__Left__To__Right {
  from {
    right: 20%;
  }
  to {
    right: -100%;
  }
}

